'use client';
import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import { RiArrowRightDoubleFill } from 'react-icons/ri';
import { MdArrowRightAlt } from 'react-icons/md';
import NewSidebar from '../components/NewSideBar/page';

export default function Projects() {
  const [projects, setProjects] = useState([]);
  const { bearerKey } = useAuth();

  useEffect(() => {
    const fetchProjects = async () => {
      const response = await fetch('http://localhost:8081/projects', {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${bearerKey}`,
          'Content-Type': 'application/json',
        },
      });

      const data = await response.json();
      console.log('DATA', data._embedded.projects);
      setProjects(data._embedded.projects);
    };

    fetchProjects();
  }, [bearerKey]);

  return (
    <div className="p-4 pt-16 bg-[#eff8fb] px-32 h-[100vh] relative">
      <NewSidebar /> {/* Sidebar componentini ekleyin */}
      {/* <div className="mb-8 mt-6">
        <img className="w-32" src="./images/lightWhiteLogo.png" alt="logo" />
      </div> */}
      <div className="flex justify-between items-center mb-16">
        <h1 className="text-[40px] font-bold text-[#0000cd]">Projelerim</h1>
        <select className="p-2 border rounded text-black ">
          <option value="" disabled hidden selected>
            Sırala
          </option>
          <option className="text-[#cae1ff]">Yakın Tarihe Göre Sırala</option>
          <option className="text-[#cae1ff]">
            Bitme Yüzdesine Göre Sırala
          </option>
        </select>
      </div>
      <div className="space-y-4">
        {/* Başlıklar */}
        <div className="grid grid-cols-5 gap-4 font-bold border-b pb-2 text-[#0000cd] text-[18px] text-center">
          <span>Proje Adı</span>
          <span>Başlangıç Tarihi</span>
          <span>Bitiş Tarihi</span>
          <span>Proje Türü</span>
          <span></span> {/* Boş alan, ikon için sütun başlığı olmayacak */}
        </div>
        <div className="max-h-[600px] h-[75vh] overflow-y-auto">
          {/* Kartlar */}
          {projects.map((project) => (
            <div
              key={project.projectId}
              className="grid grid-cols-5 mt-4 gap-4 p-4 border rounded-lg shadow-sm items-center bg-[#0000cd] text-[white] text-center"
            >
              <span className="text-lg font-semibold">
                {project.projectName}
              </span>
              <span>
                {new Date(project.projectStartDate).toLocaleDateString()}
              </span>
              <span>
                {new Date(project.projectEndDate).toLocaleDateString()}
              </span>
              <span>{project.projectType}</span>
              <span className="flex justify-end">
                <RiArrowRightDoubleFill color="#001A78" size={30} />
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
